
import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import * as serviceWorker from './serviceWorker';
import data from './RawData'

class Fruits extends Component {
    constructor() {
        super();
        this.state = {
            phone: false,
        }
    }

    componentDidMount() {
        if(window.innerWidth < 600) {
            this.setState({phone: true})
        } else {
            this.setState({phone: false})
        }
    }

    PhoneView() {
        return(
            <div className='card2' style={{backgroundImage: this.props.img }}>
                <div className="Ddiv2"><h3>
                    Fat: <p>{this.props.fat} g</p>
                    <br></br>Carbohydrate: <p>{this.props.carb} g</p>
                    <br></br>Dietary fiber: <p>{this.props.fiber} g</p>
                    <br></br>Sugar: <p>{this.props.sugar} g</p>
                    <br></br>Protein: <p>{this.props.protein} g</p>
                    </h3>
                </div>
                <div className='footer2'>
                    <h2>{this.props.name}</h2><br></br>
                    <h5>Calories: {this.props.cal} kcal</h5><br></br>
                    <h6>Per 100 grams</h6>
                </div>
            </div>
        )
    }

    DexView() {
        return( 
            <div className='card' style={{backgroundImage: this.props.img }}>
                <div className="Ddiv"><h3>
                    Fat: <p>{this.props.fat} g</p>
                    <br></br>Carbohydrate: <p>{this.props.carb} g</p>
                    <br></br>Dietary fiber: <p>{this.props.fiber} g</p>
                    <br></br>Sugar: <p>{this.props.sugar} g</p>
                    <br></br>Protein: <p>{this.props.protein} g</p>
                    </h3>
                </div>
                <div className='footer'>
                    <h4>{this.props.name}</h4><br></br>
                    <h5>Calories: {this.props.cal} kcal</h5>
                    <h6>Per 100 grams</h6>
                </div>
            </div>
        )
    }

    render() {      
        if(this.state.phone) {
            return(
                this.PhoneView()
            )
        } else {
            return(
                this.DexView() 
            )
        }
    }
}

