class Show extends Component {

    constructor() {
        super();
        this.state = {
            fruitData: this.mapData(data),
            ar: false
        }

        this.search=this.search.bind(this)
        this.AlphaSort=this.AlphaSort.bind(this)
        this.CalSort=this.CalSort.bind(this)
        this.SugarSort=this.SugarSort.bind(this)
        this.CarbsSort=this.CarbsSort.bind(this)
        this.FiberSort=this.FiberSort.bind(this)
        this.ProteinSort=this.ProteinSort.bind(this)
        this.Lang=this.Lang.bind(this)
        this.changeHandle=this.changeHandle.bind(this)
    }

    mapData(arr) {
        return arr.map(el => <Fruits key={el.id} img={el.img} name={el.name} arname={el.arname} cal={el.cal} fat={el.fat} carb={el.carb} fiber={el.fiber} sugar={el.sugar} protein={el.protein} />);
    }

    mapDataAR(arr) {
        return arr.map(el => <FruitsAR key={el.id} img={el.img} name={el.name} arname={el.arname} cal={el.cal} fat={el.fat} carb={el.carb} fiber={el.fiber} sugar={el.sugar} protein={el.protein} />);
    }

    render() {
        return (
            <div>
                <nav id='navbar' className='srchConrainer'>
                    <button id='lang' onClick={this.Lang}>العربية</button>
                    <input id='searchInput' type='text' placeholder="search" onChange={this.search}></input><br></br>
                    <select id="sort" onChange={this.changeHandle}>
                    <option id="choose" value='default'disabled>Sort By</option>
                    <option id="AlphaSort" value="AlphaSort" onClick={this.AlphaSort}>Alphabetical</option>
                    <option id="CalSort" value="CalSort" onClick={this.CalSort}>Kcal</option>
                    <option id="SugarSort" value="SugarSort" onClick={this.SugarSort}>Sugar</option>
                    <option id="CarbsSort" value="CarbsSort" onClick={this.CarbsSort}>Carbs</option>
                    <option id="FiberSort" value="FiberSort" onClick={this.FiberSort}>Fiber</option>
                    <option id="ProteinSort" value="ProteinSort" onClick={this.ProteinSort}>Protein</option>
                    </select>
                </nav>
                {this.state.fruitData}
            </div>
        )
    }

    Lang () {
        if (this.state.ar){
            this.setState({fruitData: this.mapData(data),
            ar: false});
            document.getElementById('lang').innerHTML='العربية'
            document.getElementById('searchInput').value = ""
            document.getElementById('choose').innerHTML='Sort By'
            document.getElementById('choose').selected='defaultvalue'
            document.getElementById('AlphaSort').innerHTML='Alphabetical'
            document.getElementById('CalSort').innerHTML='Kcal'
            document.getElementById('SugarSort').innerHTML='Sugar'
            document.getElementById('CarbsSort').innerHTML='Carbs'
            document.getElementById('FiberSort').innerHTML='Fiber'
            document.getElementById('ProteinSort').innerHTML='Protein'
            document.getElementById("searchInput").placeholder = 'Search'
            document.getElementById('root').style.direction='ltr'
        } else {
            this.setState({fruitData: this.mapDataAR(data),
            ar: true});
            document.getElementById('lang').innerHTML='English'
            document.getElementById('searchInput').value = "";
            document.getElementById('choose').selected= 'defaultvalue'
            document.getElementById('choose').innerHTML='ترتيب حسب'
            document.getElementById('AlphaSort').innerHTML='الأبجدية'
            document.getElementById('CalSort').innerHTML='الطاقة'
            document.getElementById('SugarSort').innerHTML='السكر'
            document.getElementById('CarbsSort').innerHTML='السكريات'
            document.getElementById('FiberSort').innerHTML='الألياف'
            document.getElementById('ProteinSort').innerHTML='البروتين'
            document.getElementById("searchInput").placeholder = 'بحث'
            document.getElementById('root').style.direction='rtl'
        }

    }

    changeHandle(){
        let sort = document.getElementById("sort").value;
        if (sort === "AlphaSort") {
            this.AlphaSort();
        } else if (sort === "CalSort") {
            this.CalSort();
        } else if (sort === "SugarSort") {
            this.SugarSort();
        } else if (sort === "CarbsSort") {
            this.CarbsSort();
        } else if (sort === "FiberSort") {
            this.FiberSort();
        } else if (sort === "ProteinSort") {
            this.ProteinSort();
        }
    }

    AlphaSort() {
        if (this.state.ar) {
            let AlphaSorted = data.sort((a, b) => (a.arname > b.arname) ? 1 : -1);
            let AlphaMap = this.mapDataAR(AlphaSorted);
            this.setState({fruitData: AlphaMap});
        } else {
            let AlphaSorted = data.sort((a, b) => (a.name > b.name) ? 1 : -1);
            let AlphaMap = this.mapData(AlphaSorted);
            this.setState({fruitData: AlphaMap});
        }
    }

    CalSort() {
        if (this.state.ar) {
            let CalSorted = data.sort((a, b) => (a.cal < b.cal) ? 1 : -1);
            let calMap = this.mapDataAR(CalSorted);
            this.setState({fruitData: calMap});
        } else {
            let CalSorted = data.sort((a, b) => (a.cal < b.cal) ? 1 : -1);
            let calMap = this.mapData(CalSorted);
            this.setState({fruitData: calMap});
        }
    }

    SugarSort() {
        if (this.state.ar) {
            let SugarSorted = data.sort((a, b) => (a.sugar < b.sugar) ? 1 : -1);
            let SugarMap = this.mapDataAR(SugarSorted);
            this.setState({fruitData: SugarMap});
        } else {
            let SugarSorted = data.sort((a, b) => (a.sugar < b.sugar) ? 1 : -1);
            let SugarMap = this.mapData(SugarSorted);
            this.setState({fruitData: SugarMap});
        }
    }
    
    CarbsSort() {
        if (this.state.ar){
            let CarbsSorted = data.sort((a, b) => (a.carb < b.carb) ? 1 : -1);
            let CarbMap = this.mapDataAR(CarbsSorted);
            this.setState({fruitData: CarbMap});
        } else {
            let CarbsSorted = data.sort((a, b) => (a.carb < b.carb) ? 1 : -1);
            let CarbMap = this.mapData(CarbsSorted);
            this.setState({fruitData: CarbMap});
        }
    }
    
    FiberSort() {
        if (this.state.ar){
            let FiberSorted = data.sort((a, b) => (a.fiber < b.fiber) ? 1 : -1);
            let FiberMap = this.mapDataAR(FiberSorted);
            this.setState({fruitData: FiberMap})
        } else {
            let FiberSorted = data.sort((a, b) => (a.fiber < b.fiber) ? 1 : -1);
            let FiberMap = this.mapData(FiberSorted);
            this.setState({fruitData: FiberMap});
        }
    }
    
    ProteinSort() {
        if (this.state.ar){
            let ProteinSort = data.sort((a, b) => (a.protein < b.protein) ? 1 : -1);
            let ProteinMap = this.mapDataAR(ProteinSort);
            this.setState({fruitData: ProteinMap})
        } else {
            let ProteinSort = data.sort((a, b) => (a.protein < b.protein) ? 1 : -1);
            let ProteinMap = this.mapData(ProteinSort);
            this.setState({fruitData: ProteinMap});
        }
    }

    search(event) {
        if (this.state.ar) {
            let srchInput = event.target.value
            let srch = data.filter(o => o.arname.toLowerCase().indexOf(srchInput.toLowerCase()) !== -1);
            let srchMap = this.mapDataAR(srch);
            this.setState({fruitData: srchMap});
        } else {
            let srchInput = event.target.value
            let srch = data.filter(o => o.name.toLowerCase().indexOf(srchInput.toLowerCase()) !== -1);
            let srchMap = this.mapData(srch);
            this.setState({fruitData: srchMap});
        }
    }
}

